#searchtitle{
    text-align: center;
    color: white;
    margin: 0px;
    font-size: 30px;
    padding: 10px;
}
#searchform {
display: flex;
justify-content: center;
align-items: center;
gap: 48px;
margin: 30px;

}
.inputs {
    font-size: 30px;
    text-align: center;
  border-radius: 20px;
    
}
#searchbutton{
font-size: 30px;
cursor: pointer;
border-radius: 10px;

}
button:hover{
    background-color: aqua;
}
#searchcomponent{
    background-color: black;
    height: 200px;
    margin: 40px;
  animation: electric 1s infinite alternate; 
border-radius: 20px;
}
@keyframes electric {
    0% {
      box-shadow: 0 0 5px 2px aqua, 0 0 10px 5px aqua;
    }
    100% {
      box-shadow: 0 0 10px 5px aqua, 0 0 15px 10px aqua;
    }
  }
  @keyframes whitec {
    0% {
      box-shadow: 0 0 5px 2px white, 0 0 10px 5px white; /* Adjust the color as needed */
    }
    100% {
      box-shadow: 0 0 10px 5px white, 0 0 15px 10px white; /* Adjust the color as needed */
    }
  }
  @media screen and (max-width: 600px) {
    #searchtitle{
