#footerbox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-image: url("../imgs/test.gif");
  background-repeat: no-repeat;
  background-size: cover;
  color: black;
  position: relative;
  overflow: hidden;
  height: 40px;
  animation: electric-footer 1s infinite alternate; /* Adjust the animation duration as needed */
}
h1 {
  margin: 0;
  text-align: center;
  font-size: 70px;
}
@keyframes electric {
  0% {
    box-shadow: 0 0 5px 2px aqua, 0 0 10px 5px aqua;
  }
  100% {
    box-shadow: 0 0 10px 5px aqua, 0 0 15px 10px aqua;
  }
}
@media screen and (max-width: 600px) {
  #footerbox {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-image: url("../imgs/test.gif");
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    position: relative;
    overflow: hidden;
    height: 30px;
    animation: electric-footer 1s infinite alternate; /* Adjust the animation duration as needed */
  }
  h1 {
    margin: 0;
    text-align: center;
    font-size: 30px;
  }

}