#datacontainer{
animation: electric-footer 1s infinite alternate;
margin: 5dvh;
height: 600px;
background-color: black;
}
h2, div{
    color: white;
    font-weight: bold;
}
#title{
font-size: 50px;
text-align: center;
}
.text{
  font-size: 20px;
}
#info-container{
    display: flex;
    justify-content:space-evenly;
    flex-wrap: wrap;
    gap: 30px;
    padding: 30px;
}
#noresults{
  font-size: 100px;
  text-align: center;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.icons {
height: 152px;
width: 152px;
}
.image-container {
  position: relative;
  display: inline-block;
}

.image-container img {
  display: block;
}

.overlay-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #000000;
  color: #fff;
  padding: 10px 20px;
  font-size: 20px;
  text-align: center;
  opacity: 0; 
  transition: opacity 0.3s ease; 
}

.image-container:hover .overlay-text {
  opacity: 1; 
}
#noinfo{
  width: 700px;
  height: 400px;
animation: whitec 1s infinite alternate;
}
@-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes electric-footer {
    0% {
      box-shadow: 0 0 5px 2px aqua, 0 0 10px 5px aqua;
    }
    100% {
      box-shadow: 0 0 10px 5px aqua, 0 0 15px 10px aqua; 
    }
  }
  @keyframes whitec {
    0% {
      box-shadow: 0 0 5px 2px white, 0 0 10px 5px white; 
    }
    100% {
      box-shadow: 0 0 10px 5px white, 0 0 15px 10px white; 
    }
  }
