#map-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px auto; 
}

#map {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  align-content: center;
  width: 94%;
  height: 600px;
  padding: 0px;
  animation: electric 1s infinite alternate;

}
@keyframes electric {
  0% {
    box-shadow: 0 0 5px 2px aqua, 0 0 10px 5px aqua;
  }
  100% {
    box-shadow: 0 0 10px 5px aqua, 0 0 15px 10px aqua;
  }
}
@media screen and (max-width: 600px) {
  #map-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px auto; 
  }
  
  #map {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    width: 94%;
    height: 300px;
    padding: 0px;
    animation: electric 1s infinite alternate;
  
  }
}
